.nav {
  display: flex;
  align-content: center;
  justify-content: start;
  width: 100%;
  height: 75px;
  margin-top: 15px;
  list-style: none;
  background-color: #222222;
}

.link {
  margin-top: 20px;
  margin-left: 5px;
  width: 125px;
  height: 65px;
  transition: background-position-x 0.9s linear;
  text-align: center;
}

.link {
  font-size: 22px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  transition: all 0.45s;
}

