.botLink {
  font-size: 22px;
  color: rgb(24, 24, 24);
  text-decoration: none;
  transition: all 0.45s;
  margin-top: 20px;
  margin-left: 5px;
  width: 125px;
  height: 65px;
  text-align: center;
}

.botLink:hover {
  color: #d94f5c;
}
.botLink:not(:last-child) {
  margin-right: 30px;
}

@keyframes line {
  0% {
    background-position-x: 390px;
  }
}

.btn_back {
  width: 150px;
  height: 60px;
  background-color: #d94f5c;
  color: white;
  cursor: pointer;
  text-align: center;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
}

.btn_back:hover {
  background-color: salmon;
}